/* Header style */
.main-header {
    background: linear-gradient(360deg, #b9d9bd8f, transparent);
    padding: 2em;
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: #345234;
  }
  .main-header-logo {
    display: flex;
    align-items: center;
    font-family: 'Shantell Sans', cursive;
  }
  .main-header-logo .main-header-haeding {
    font-family: 'Shantell Sans', cursive;
  }
  
  .main-header .all-books-btn {
    font-weight: bold;
    color: #345234;
    padding: 0.5em 1em;
    border: 1px solid #a4aea4;
    background: #aeaf9836;
    border-radius: 0.5em;
  }